body {
  background-image: url("Netflix_img.jpg");
}
nav {
  display: flex;
  height: 90px;
  padding: 30px 25px;
  align-items: center;
}
.tittle-name {
  font-size: 4rem;
  color: red;
  font-weight: bold;
  letter-spacing: 4px;
  font-family: "Bebas Neue", cursive;
}
.outer {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.75);
  box-sizing: border-box;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  margin: 0;
  height: 18rem;
}
@media only screen and (min-width: 740px) {
  .MainOuter {
    margin: 0 auto -236px;
    min-height: 100vh;
    background-color: transparent;
    max-width: 450px;
  }
}

@media only screen and (max-width: 400px) {
  .outer {
    height: 30rem;

    max-width: 450px;
  }
  h1 {
    text-align: center;
  }
  .tittle-name {
    text-align: center;
  }
}

.MainOuter {
  padding: 0 5%;
}

@media only screen and (min-width: 740px) {
  .outer {
    min-height: 600px;
  }
}

.outer h1 {
  color: #fff;
  font-size: 40px;
  font-weight: 700;
  padding: 50px;
}
h1 {
  margin: 0 0 10px 0;
  padding: 0;
}
.text {
  border-radius: 4px;
  border: 0;
  color: #fff;
  height: 50px;
  line-height: 50px;
  padding: 16px 20px 0;
  background: #333;
  box-sizing: border-box;
  font-size: 16px;
  display: flex;
  width: 20rem;
}
.mat {
  display: flex;
  justify-content: center;

  align-items: center;
}

.pass {
  border-radius: 4px;
  border: 0;
  color: #fff;
  height: 50px;
  line-height: 50px;
  padding: 16px 20px 0;
  background: #333;
  box-sizing: border-box;
  font-size: 16px;
  display: flex;
  width: 20rem;
}

.mat2 {
  padding-top: 40px;
}
.mat3 {
  padding-top: 40px;
}
.sub {
  width: 20rem;
  height: 50px;
  background-color: red;
  color: white;
  font-weight: bold;
  font-size: 16px;
  border-radius: 4px;
}
.remember {
  padding-left: 20px;
  padding-top: 5px;
  display: flex;
}
span {
  color: #b3b3b3;
  font-size: 13px;
  font-weight: 500;
}
.need {
  cursor: pointer;
  margin-left: 7rem;
  display: block;
}
.new {
  color: #737373;
  font-size: 16px;
  font-weight: 500;
  margin-top: 16px;
  padding-top: 3px;
  margin-left: 1rem;
}
.new a {
  color: white;
  cursor: pointer;
}

